<template>

	<div>
		<div class='creat'>
			<a-row>
				<a-col>
					<a-input-search placeholder="" style="width: 200px" @search="onSearch" />

					<a-button type="primary" icon="plus-circle" @click='showDrawer' style='float: right;'>
						
					</a-button>
					<a-drawer title="" placement="right" :closable="false" :visible="visible" width='300px'
						:after-visible-change="afterVisibleChange" @close="onClose">

						<a-form :form="form" layout='horizontal'>
							<a-form-item label="">
								<a-input v-decorator="['name', { rules: [{ required: true, message: '!' }] }]" />
							</a-form-item>
							<a-form-item label="">


								<a-input v-decorator="[
						          'password',
						          { rules: [{ required: true, message: '!' },{ min: 8, message: '8' }] },
						        ]" type='password'>
								</a-input>
							</a-form-item>

							<a-form-item label="">


								<a-input v-decorator="[
							      'alisname',
							      { rules: [{ required: true, message: '!' }] },
							    ]">
								</a-input>
							</a-form-item>

						</a-form>

						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose">
								
							</a-button>
							<a-button type="primary" @click="handleSubmit">
								
							</a-button>
						</div>
					</a-drawer>
					
					
					
					
					<a-drawer title="" placement="right" :closable="false" :visible="visible_Edit" width='300px'
						:after-visible-change="afterVisibleChange" @close="onCloseEdit">
					
						<a-form :form="formEdit" layout='horizontal'>
							<a-form-item label="" >
								<a-input v-decorator="['name', { rules: [{ required: true, message: '!' }] }]"  disabled/>
							</a-form-item>
							<a-form-item label="">
					
					
								<a-input v-decorator="[
						          'password',
						          { rules: [{ required: true, message: '!' },{ min: 8, message: '8' }] },
						        ]" type='password'>
								</a-input>
							</a-form-item>
					
							<a-form-item label="">
					
					
								<a-input v-decorator="[
							      'alisname',
							      { rules: [{ required: true, message: '!' }] },
							    ]">
								</a-input>
							</a-form-item>
					
						</a-form>
					
						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose_edit">
								
							</a-button>
							<a-button type="primary" @click="UpdateSubmit">
								
							</a-button>
						</div>
					</a-drawer>

				</a-col>


			</a-row>

		</div>
		<div id='list'>
			<a-row>
				<a-table :columns="columns" :data-source="data" :row-key="record => record.id" :pagination="pagination"
					:loading="loading" @change="" size="small">


					<div slot='name' slot-scope="text,record,index">
						<a-tag color="red">
							{{record.name}}
						</a-tag>
					</div>

					<span slot="edit" slot-scope="text, record">
						<a-space size="small">
							<a-tooltip placement="left">
								<template slot="title">
									<span></span>
								</template>
								<a-popconfirm title="?" ok-text="" cancel-text="" @confirm="del(record.id)">
									<a-icon class="pointer" type="delete" />
								</a-popconfirm>
							</a-tooltip>
							<span>|</span>

							<a-tooltip placement="top">
								<template slot="title">
									<span></span>
								</template>
								<a-icon class="pointer" type="form" @click="showEdit(record.id,record.name,record.alisname)" />
							</a-tooltip>

						</a-space>
					</span>

				</a-table>

			</a-row>
		</div>

	</div>
</template>

<script>
	const columns = [{
			title: '',
			dataIndex: 'name',
			width: '20%',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '',
			dataIndex: 'alisname',
			width: '20%',
		},
		{
			title: '',
			dataIndex: 'edit',
			align: 'center',
			scopedSlots: {
				customRender: 'edit'
			},
		},
	];


	export default {
		data() {
			return {
				columns,
				loading: false,
				data: [],
				pagination: {
					// total: this.total,
					defaultPageSize: 10,
					// pageSize.sync:'5'
					showQuickJumper: true,
					showSizeChanger: true,
					pageSizeOptions: ['10', '20', '30', '40', '50', '100'],

				},
				visible: false,
				visible_Edit:false,
				form: this.$form.createForm(this),
				formEdit:this.$form.createForm(this),

			}
		},
		methods: {
			onload() {
				this.loading = true
				this.$http.get('/api/user/list/').then((res) => {
					this.loading = false
					this.data = res.data.res
					console.log(res)
				})

			},
			onSearch(value) {
				this.$http.post('/api/user/search/',{
					'result':value
				}).then((res)=>{
					this.data = res.data.res
					this.$message.success('')
				})
				

			},
			showEdit(id,name,alisname) {
				this.visible_Edit = true
				setTimeout(() => {
					this.formEdit.setFieldsValue({
						name: name,
						alisname: alisname,
					});
				}, 100);
			},

			del(id) {
				this.$http.post('/api/user/del/',{
					id:id,
				}).then((res)=>{
					if (res.data.status === 'success') {
						this.$message.success('')
					}
					if (res.data.status === 'fail') {
						this.$message.error('')
					}
					this.onload()
				})
			},
			afterVisibleChange(val) {
				console.log('visible', val);
			},
			showDrawer() {
				this.visible = true
			},
			onClose() {
				this.visible = false
			},
			onCloseEdit(){
				this.visible_Edit = false
				
			},
			onClose_edit(){
				this.visible_Edit = false
			},
			handleSubmit() {
				this.form.validateFields((err, values) => {
					if (!err) {
						this.$http.post('/api/user/add/', {
							'username': values.name,
							'alisname': values.alisname,
							'password': values.password,
						}).then((res) => {
							console.log(res)
							if (res.data.msg === '') {
								this.$message.error('')
							}
							if (res.data.status === 'success') {
								this.$message.success('')
							}
							this.visible = false
							this.onload()
						})

					}
				})
			},
			UpdateSubmit(){
				this.formEdit.validateFields((err, values) => {
					if(!err){
						this.$http.post('/api/user/edit/',{
							'name' : values.name,
							'password':values.password,
							'alisname':values.alisname,
							
						}).then((res)=>{
							if (res.data.status === 'success') {
								this.$message.success('')
							}
							this.visible_Edit = false
							this.onload()
						})
					}
				})
				
			},



		},
		mounted() {
			this.onload()
		},

	}
</script>

<style scoped>
	.creat {
		background: #edeff3;
		border: 8px solid #edeff3;
		border-radius: 5px;
	}

	#list {
		margin-top: 10px;
	}
</style>
